<!-- -->
<template>
<div class='wrapper'>
<v-head></v-head>
  <div class="clear"></div>
 <div class="el-main">
    <div id="infoform">
    <el-form id="form1" :inline="true" :rules="rules" :model="form1" ref="form1" label-width="100px" >
     <el-form-item label="账号：" prop="student_id" >
        <el-input v-model="form1.student_id" :disabled="true" style="width:220px"></el-input>
     </el-form-item>
    <el-form-item label="姓名：" prop="s_name">
        <el-input v-model="form1.s_name" type="text" style="width:220px"></el-input>
     </el-form-item>
    
    <el-form-item label="学院：" prop="sdepartment">
        <el-input v-model="form1.sdepartment" style="width:220px"></el-input>
     </el-form-item>
    <el-form-item label="专业：" prop="smajor">
        <el-input v-model="form1.smajor" style="width:220px"></el-input>
     </el-form-item>
    <el-form-item label="班级：" prop="sclass">
        <el-input v-model="form1.sclass" style="width:220px"></el-input>
     </el-form-item>
     <el-form-item label="入学时间：" prop="stime_of_enrollment">
        <el-date-picker v-model="form1.stime_of_enrollment" @change="getEnrollTime" format="yyyy-MM-dd" id="enrollTime" type="date" style="width:220px" placeholder="选择日期" :picker-options="pickerOptions"></el-date-picker>
     </el-form-item>
    
    <el-form-item label="毕业时间：" prop="stime_of_graduation">
        <el-date-picker v-model="form1.stime_of_graduation" @change="getGraduateTime" format="yyyy-MM-dd" id="graudateTime" type="date" style="width:220px" placeholder="选择日期" ></el-date-picker>
     </el-form-item>

    <el-form-item label="职务：" prop="stitle">
        <el-input v-model="form1.stitle" type="text" style="width:220px"></el-input>
    </el-form-item>
    <el-form-item label="性别：" prop="ssex">
      <el-select v-model="form1.ssex" placeholder="请选择性别" style="width:220px" id="sex">
            <el-option label=" " value="0"></el-option>
            <el-option label="男" value="1"></el-option>
            <el-option label="女" value="2"></el-option>
    </el-select>
  
     </el-form-item>
          <el-form-item label="出生日期：" >
        <el-date-picker v-model="form1.sbirthday" @change="getBirthTime" format="yyyy-MM-dd" type="date" style="width:220px" id="birday" placeholder="选择日期" :picker-options="pickerOptions"></el-date-picker>
     </el-form-item>
        <el-form-item label="民族：" prop="snationality">
        <el-input v-model="form1.snationality" style="width:220px"  type="text"></el-input>
     </el-form-item>
       <el-form-item label="健康状态：" prop="shealth_state">
        <el-select v-model="form1.shealth_state"   placeholder="请选择健康状态" style="width:220px" id="health">
            <el-option label="健康" value="0"></el-option>
            <el-option label="亚健康" value="1"></el-option>
        </el-select>
     </el-form-item>
        <el-form-item label="籍贯：" prop="snative_place">
        <el-input v-model="form1.snative_place" style="width:220px"  type="text"></el-input>
     </el-form-item>    
         <el-form-item label="政治面貌：" prop="spolitics_status">
        <el-input v-model="form1.spolitics_status"  type="text" style="width:220px"></el-input>
     </el-form-item>
    <el-form-item label="方向：" prop="sarea_of_interest">
        <el-input v-model="form1.sarea_of_interest"  type="text" style="width:220px"></el-input>
     </el-form-item>
<el-col></el-col>
</el-form>
<el-form id="form2" :inline="true" label-width="150px" >
<el-form-item>
        <el-button type="primary" @click="modifyForm('form1')">保存</el-button>
        <el-button type="success" @click="resetForm('form1')">重置</el-button>
        <el-button @click="exit('form1')">返回</el-button>
     </el-form-item>
    
     </el-form>
        </div>
      </div>
</div>

</template>
    
<script>
import vHead from '../commons/AdminstratorNav.vue';
export default {
    created(){
        localStorage.setItem("first","1");
        this.form1.student_id=localStorage.getItem("selectRow");
        var self=this;
        $.ajax({
            type:'post',
            dataType:'json',
            url:'/student/fileInfo',
            data:{
                "id":this.form1.student_id
            },
            success:function(response){
              console.log(response);
                var array =  response.stringbirthDay.split("-");
                var birday = new Date(array[0], array[1], array[2]);
                var array=response.stringtimeEnrollment.split("-");
                var enrollTime=new Date(array[0], array[1], array[2]);
                var array=response.stringtimeGraducation.split("-");
                var graduateTime=new Date(array[0], array[1], array[2])

                self.form1.s_name=response.name;
                self.form1.snationality=response.nationality;
                self.form1.sdepartment=response.depName;
                self.form1.smajor=response.majName;
                self.form1.sclass=response.claName;
                self.form1.snative_place=response.nativePlace;
                self.form1.spolitics_status=response.politicalStatus;
                self.form1.ssex=self.getSex(response.sex);
                self.form1.sarea_of_interest=response.areaInterest;
                self.form1.stitle=response.title;
                self.form1.shealth_state=response.healthState;
                self.form1.sbirthday=birday;
                self.form1.stime_of_enrollment=enrollTime;
                self.form1.stime_of_graduation=graduateTime;
                console.log(localStorage)
            },
            error:function(err){
                console.log(err);
                console.log("error");
            }
        })

    },
    components:{
        vHead,
    },
  data(){
      return{
          hostUrl:'/',
          check1: false,
          pickerOptions: {
          disabledDate(time) {
            return time.getTime() > Date.now();
          }
        },
          form1:{
              student_id: '',
              s_name: '',
              sdepartment:'',
              smajor:'',
              sclass:'',
              stime_of_enrollment:'',
              stime_of_graduation:'',
              stitle:'',
              ssex:'',
              sbirthday:'',
              snationality:'',
              shealth_state:'',
              snative_place:'',
              spolitics_status:'',
              sarea_of_interest:''
          },
		  rules: {
          student_id: [
            { required: true, message: '请输入学号', trigger: 'blur' },
            { min: 12, max: 12, message: '长度为12个字符', trigger: 'blur' }
          ],
          s_name: [
            { required: true, message: '输入名字', trigger: 'blur' },
          ],
          sdepartment: [
              { required: true, message: '请输入学生学院', trigger: 'blur' }
          ],
           smajor: [
              { required: true, message: '请输入学生专业', trigger: 'blur' }
          ],
          sclass:[
               { required: true, message: '请输入学生班级', trigger: 'blur' }
          ],
         stime_of_enrollment:[
             { type: 'date', required: true, message: '请选择时间', trigger: 'change' },
         ],
         stime_of_graduation:[
             { type: 'date', required: true, message: '请选择时间', trigger: 'change' },
         ],
		 stitle:[
             { required: true, message: '请选择职务', trigger: 'change' },
         ],
         ssex:[
             { required: true, message: '请选择性别', trigger: 'change' },
         ],
		 sbirthday:[
             { type: 'date', required: true, message: '请选择时间', trigger: 'change' },
         ],
		  snationality:[
             { required: true, message: '请输入民族', trigger: 'change' },
         ],
		 shealth_state:[
             { required: true, message: '请输入健康状态', trigger: 'change' },
         ],
		  snative_place:[
             { required: true, message: '请输入籍贯', trigger: 'change' },
         ],
		 spolitics_status:[
             { required: true, message: '请输入政治面貌', trigger: 'change' },
         ],
		 sarea_of_interest:[
             { required: true, message: '请输入方向', trigger: 'change' },
         ],

        }
      };
  },
  methods:{
      getEnrollTime(date){
               this.form1.stime_of_enrollment=date;
               console.log(typeof this.form1.stime_of_enrollment);
           },
           getGraduateTime(date){
               this.form1.stime_of_graduation=date;
               console.log(typeof this.form1.stime_of_graduation);
          },
          getBirthTime(date){
              this.form1.sbirthday=date;
               console.log(typeof this.form1.sbirthday);
          },
          getSex(sex){
              if(sex==0)
              return "0"
              else if(sex==1)
              return "1"
              else return "2"
          },
      
        modifyForm(formName) {
        console.log(this.form1.stitle);
        console.log(this.form1.stime_of_enrollment);
        var vm=this;
        const h = this.$createElement;
        this.$msgbox({
          title: '消息',
          message: h('p', null, [
            h('span', null, '是否确定要'),
            h('i', { style: 'color: teal' }, '修改')
          ]),
          showCancelButton: true,
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          beforeClose: (action, instance, done) => {
            if (action === 'confirm') {
              console.log(this.form1.stime_of_enrollment);
              console.log(typeof this.form1.stime_of_enrollment);
              $.ajax({
                type:'post',
                url:'/student/setInfo',
                dataType:'json',
                // async:false,
                data:{
                  "id":this.form1.student_id,
                  "nationality":this.form1.snationality,
                  "nativePlace":this.form1.snative_place,
                  "politicalStatus":this.form1.spolitics_status,
                  "sex":this.form1.ssex,
                  "areaInterest":this.form1.sarea_of_interest,
                  "health":this.form1.shealth_state,
                  "title":this.form1.stitle,
                  "enrollTime":this.form1.stime_of_enrollment,
                  "graduateTime":this.form1.stime_of_graduation,
                  "birthday":this.form1.sbirthday
                },
                success:function(response){
                    
                    if(response.data===1){
                       vm.$message({
                            type: 'success',
                            message: '修改成功!'
                       })
                    }else{
                       vm.$message({
                            type: 'info',
                            message: '网络错误!'
                       })
                    }
                },
                error:function(err){
                    console.log(err)
                }
            })
            done();
            } else {
              console.log("11111111111111111");
               vm.$message({
                        type: 'info',
                        message: '已取消修改'
                      });    
              done();
            }
          }
        })        

        },
      resetForm(formName) {
        this.$refs[formName].resetFields();
      },
      exit(formName) {
        this.$router.push({path: '/adminstrator/studentInfo'})
      }
    }
  }
</script>
<style scoped>
.clear{
  clear: both;
}
.el-main{
    margin-top:3px;
    background-color:#eef1f6;
    color: #333;
    text-align: center;
    height:800px;
}
#infoform{
    padding-top: 60px;
}
#form1{
    width:80%;
    margin:0px auto;
}
#form2{
    width:220px;
    margin:0px auto;
    float: center;
}
  .el-form-item{
      float:left;
  }
</style>


